<template>
	<div class="search-main">
	<section>
        <b-field>
            <b-input placeholder="搜索"
                type="search"
                icon="magnify" expanded>
            </b-input>
            <p class="control">
                <button class="button"><i class="fa fa-search"></i></button>
            </p>
        </b-field>
	</section>        
	<div class="container">
		<my-tag />
	</div>
	</div>
</template>
<script>
	import tag from './tag.vue'
	export default{
		name:'search',
		components:{
			'my-tag':tag
		}
	}
</script>
<style scoped>
	.search-main{background: #f7f7f7}
</style>


