import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import detail from '@/components/detail'
import search from '@/components/search'
import topic from '@/components/topic'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
    	path:'/search',
    	name:'search',
    	component: search
    },
    {
    	path:'/detail',
    	name:'detail',
    	component: detail
    },
    {
      path:'/topic',
      name:'topic',
      component: topic
    }
  ]
})



