<template>
<div class="home">
<section class="hero is-info is-medium">
  <!-- Hero head: will stick at the top -->
  <div class="hero-head" v-bind:style="{background:headerback,position:headerposi}">
    <nav class="navbar">
      <div class="container">
        <div class="navbar-brand">
          <span class="navbar-burger burger" @click="showmenu" data-target="navbarMenuHeroA">
            <span></span>
            <span></span>
            <span></span>
          </span>
          <span class="navbar-burger burge" v-bind:style="{opacity:logoopacity}">
            <h4>哈士猫</h4>
          </span>
          <span class="navbar-burger burge icon">
            <a href="#/search"><i class="fa fa-search"></i></a>
          </span>
        </div>
        <div id="navbarMenuHeroA" v-bind:style="{display:show}" class="navbar-menu">
          <div class="navbar-end">
            <a class="navbar-item is-active">
              首页
            </a>
            <a class="navbar-item">
              文章
            </a>
            <a class="navbar-item">
              社区
            </a>
          </div>
        </div>
      </div>
    </nav>
  </div>

  <!-- Hero content: will be in the middle -->
  <div class="hero-body">
    <div class="container has-text-centered">
      <h1 class="title">
        hashimao.com
      </h1>
      <h2 class="subtitle">
        专业的宠物社区
      </h2>
    </div>
  </div>

  <!-- Hero footer: will stick at the bottom -->
  <div class="hero-foot">
    <nav class="tabs">
      <div class="container">
        <ul>
          <li class="is-active"><a>首页</a></li>
          <li><a>文章</a></li>
          <li><a>问答</a></li>         
        </ul>
      </div>
    </nav>
  </div>
</section>
<div class="container is-fullhd">
  <my-tag />
  <my-list />
</div>
</div>
</template>
<script>
import Buefy from 'buefy'
import list from './list.vue'
import tag from './tag.vue'

export default {
	name:'index',
  data(){
    return {
      headerback:'rgba(0,0,0,0)',
      show:'none',
      headerposi:'relative',
      logoopacity:0
    }
  },
  components: {
    'my-list': list,
    'my-tag':tag
  },
  mounted:function() {
    var that = this;
      window.addEventListener('scroll',function(e){
        var percent = window.pageYOffset/150;
        if (window.pageYOffset == 0) {
          that.headerposi = 'relative';
        }else{
           that.headerposi = 'fixed';
        }       
        that.headerback = 'rgba(0,0,0,'+percent+')';
        if (window.pageYOffset >100) {
          that.logoopacity = percent;
        }else{
          that.logoopacity = 0
        }
      })
  },
  methods:{
  showmenu(e){
    this.show = this.show == 'none'?'block':'none';
  }
}
}
</script>
<style scoped>
  .hero-head{
    width: 100%;
    z-index: 999
  }
  .home{
    background:#f9f9f9;
  }
  .hero.is-info{
    background: url('../assets/img/banner.jpg');
    background-size: cover;    
  }
  .navbar-brand .navbar-burger:nth-child(1){
    margin-left: 0;
  }
  .navbar-brand .navbar-burger{line-height: 3.25rem;}
    .navbar-brand .navbar-burger .fa{font-size: 1.2em;margin-left: 1rem;}
  .hero.is-info .navbar-menu{    
    background: rgba(0,0,0,0.8);
    position: absolute;
    width: 100%;
    z-index: 999;
  }
  .hero.is-info a.navbar-item:hover, .hero.is-info a.navbar-item.is-active, .hero.is-info .navbar-link:hover, .hero.is-info .navbar-link.is-active{
    background: rgba(0,0,0,0.5);
  }
  .tagtitle{border-left: 2px solid #3273dc;margin:0 0 0.8rem 0;padding-left:0.5rem;font-size: 0.8rem}
</style>


