<template>
	<section class="hero is-info is-medium">
		  <!-- Hero head: will stick at the top -->
		  <div class="hero-head">
		    <nav class="navbar">
		      <div class="container">
		        <div class="navbar-brand">
		          <span class="navbar-burger burge icon">
		            <a v-on:click="goback"><i class="fa fa-chevron-left"></i></a>
		          </span>
		          <span class="navbar-burger burge icon">
		            <a><i class="fa fa-ellipsis-v"></i></a>
		          </span>
		        </div>
		      </div>
		    </nav>
		  </div>
		</section>
</template>
<script>
	import router from '../router/index'
	export default{
		name:'header',
		methods:{
			goback:function(){
				router.go(-1);
			}
		},
	}
</script>
<style scoped>
	.hero-head{
		background: #fff;
	    color: #aaa;
	    border-bottom: 1px solid #eee;
	}
	.navbar-brand .navbar-burger:nth-child(1){
    	margin-left: 0rem;    	
	}
    .navbar-brand .navbar-burger{line-height: 3.25rem;text-align: center;}
</style>


